.services {
  background-color: $color-black-pearl;
  color: $color-default-white;
}

.services__wrapper {
  padding-right: 20px;
  padding-left: 20px;
  padding-top: 65px;
  padding-bottom: 140px;

  @include vp-1439 {
    padding: 65px 20px 140px;
  }

  @include vp-1023 {
    padding-top: 23px;
    padding-bottom: 75px;
  }

  @include vp-767 {
    padding-top: 23px;
    padding-bottom: 85px;
  }
}

.services__wrapper h2 {
  margin: 0;
  margin-bottom: 55px;

  font-family: $ff-roboto-condensed;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;

  @include vp-1023 {
    margin-bottom: 38px;
    margin-top: 33px;

    font-size: 36px;
    line-height: 51px;
  }

  @include vp-767 {
    margin-bottom: 30px;
    margin-top: 22px;

    font-size: 28px;
    line-height: 140%;
  }
}

.services__list {
  display: grid;
  grid-template-columns: repeat(3, minmax(200px, 350px));
  gap: 85px;

  margin: 0;
  padding: 0;

  list-style: none;
  box-sizing: border-box;

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 30px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 30px;
  }
}

.services__title--tablet {
  display: block;

  @include vp-767 {
    display: none;
  }
}

.services__title--mobile {
  display: none;

  @include vp-767 {
    display: block;
  }
}

.services__item {
  position: relative;
  background-image: url("../../img/intro.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: $color-black-pearl;

  @include retina {
    background-image: url("../../img/intro@2x.webp");
  }

  &:nth-child(3n+1) {
    background-size: 430%;
    background-position: 17% 70%;

    @include vp-1023 {
      background-size: 229%;
      background-position: 30% 96%;
    }

    @include vp-767 {
      background-size: 550%;
      background-position: 30.5% 53%;
    }
  }

  &:nth-child(3n+2) {
    background-size: 430%;
    background-position: 47% 70%;

    @include vp-1023 {
      background-size: 229%;
      background-position: 76% 58%;
    }

    @include vp-767 {
      background-size: 550%;
      background-position: 29.5% 95%;
    }
  }

  &:nth-child(3n+3) {
    background-size: 430%;
    background-position: 78% 70%;

    @include vp-1023 {
      background-size: 229%;
      background-position: 19% 67%;
    }

    @include vp-767 {
      background-size: 550%;
      background-position: 72.5% 70%;
    }
  }

  ::after {
    content: "";
    position: absolute;
    width: 60px;
    height: 60px;
    bottom: 0;
    right: 0;

    background-color: $color-deep-sky-blue;
    background-image: url("../../img/svg/arrow.svg");
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 3px 0 0 0;

    @include vp-767 {
      width: 40px;
      height: 40px;

      background-size: 12%;
    }
  }

  @include hover-focus {
    outline: 1px solid $color-deep-sky-blue;

    & h3 {
      color: $color-deep-sky-blue;
    }
  }
}

.services__item a {
  color: $color-default-white;
}

.services__item h3 {
  width: 60%;
  margin: 0;
  padding-top: 48px;
  padding-left: 40px;

  font-family: $ff-roboto-condensed;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;

  @include vp-1023 {
    width: 25%;
    padding-top: 35px;
    padding-left: 45px;

    font-size: 20px;
    line-height: 30px;
  }

  @include vp-767 {
    width: 65%;
    padding-top: 26px;
    padding-left: 0;

    font-size: 18px;
    line-height: 30px;
  }
}

.services br {
  display: block;

  @include vp-1023 {
    display: none;
  }

  @include vp-767 {
    display: none;
  }
}

.services__link {
  position: relative;

  display: block;
  height: 100%;
  width: 100%;
  min-height: 650px;

  cursor: pointer;

  @include vp-1023 {
    min-height: 250px;
  }

  @include vp-767 {
    padding-left: 30px;
    padding-right: 70px;
    min-height: 250px;

    font-size: 18px;
    line-height: 30px;
  }
}
